const digits = ["", "", "", "", "", "", "", "", "", ""];
const smallUnits = ["", "", "", ""];
const largeUnits = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];

export function formatKrNumber(num: string, omitOne: boolean = true): string {
    num = num.trimStart();
    let decimalPos;

    for (let i = 0; i < num.length; i++) {
        if (num[i] === '.' && decimalPos === undefined) {
            decimalPos = i;
        } else {
            const code = num.charCodeAt(i);
            if (code < 0x30 || code > 0x39) {
                if (i === 0) return "";
                num = num.slice(0, i);
                break;
            }
        }
    }

    let s = "";

    const length = decimalPos ?? num.length;
    const numChunks = Math.ceil(length / 4);
    for (let i = 0, o = length - 4; i < numChunks; i++, o -= 4) {
        let sum = 0;
        let chunk = "";
        for (let j = 3; j >= 0; j--) {
            let c = num.charCodeAt(o + 3 - j) - 0x30;
            if (!(c > 0)) continue;
            if (!omitOne || c !== 1 || ((sum !== 0 || i !== 1) && j === 0)) {
                chunk += digits[c];
            }
            chunk += smallUnits[j];
            sum += c;
        }
        chunk += largeUnits[i];
        if (sum === 0) continue;
        if (i > 0) s = " " + s;
        s = chunk + s;
    }

    if (decimalPos < num.length - 1) {
        s += "  ";
        for (let i = decimalPos + 1; i < num.length; i++) {
            const digit = num.charCodeAt(i) - 0x30;
            s += digits[digit];
        }
    }

    return s;
}